import React, { FC, useState } from "react";
import { connect } from "react-redux";
import { Dispatch } from "redux";
import { useForm } from "react-hook-form";
import { FormInput } from "../../components/FormInput/FormInput";
import { fetchCreatePhoto } from "../../redux/image/actions";
import { RootState } from "../../redux/RootReducer";
import "./create-promocode.scss";
import { useHistory } from "react-router-dom";
import { fetchCreatePromocode } from "../../redux/promocode/actions";
import { Promocode } from "../../common/types/Types";

type CreatePromocode = {
  code: string;
  discount: number;
  order_price: number;
  uses_count: number;
  cash_discount: boolean;
  percentage_discount: boolean;
};

interface CreatePromocodeProps {
  fetchCreatePromocode: (created_promocode: Promocode, history: any) => void;
}

const CreatePromocodeContainer: FC<CreatePromocodeProps> = ({
  fetchCreatePromocode,
}) => {
  let history = useHistory();
  const { register, handleSubmit, errors, setValue } = useForm();

  const onSubmit = (data: CreatePromocode) => {
    fetchCreatePromocode(
      {
        code: data.code,
        type: data.cash_discount ? "CASH" : "PERCENT",
        uses_count: data.uses_count,
        discount: Number(data.discount),
        order_price: data.order_price ? Number(data.order_price) : 0,
      },
      history
    );
  };

  return (
    <div className="Promocode-Create">
      <div className="Promocode-Create-Header">
        <p className="Promocode-Create-Header-Title">Creat Promocode</p>
        <div className="Promocode-Create-Header-Button"></div>
      </div>
      <form className="Promocode-Create-Form" onSubmit={handleSubmit(onSubmit)}>
        <p className="Promocode-Create-Form-Title">Promocode</p>
        <FormInput
          placeholder={"Code"}
          name={"code"}
          type={"text"}
          errors={errors}
          register={register}
          required={{ required: true }}
        />
        <p className="Promocode-Create-Form-Title">Number of uses(Default 1)</p>
        <FormInput
          placeholder={"Number of uses"}
          name={"uses_count"}
          type={"number"}
          errors={errors}
          register={register}
          required={{}}
        />
        <p className="Promocode-Create-Form-Title">Minimal order price</p>
        <FormInput
          placeholder={"Minimum order price"}
          name={"order_price"}
          type={"number"}
          errors={errors}
          register={register}
          required={{}}
        />
        <p className="Promocode-Create-Form-Title">Promocode type</p>
        <div className="Promocode-Create-Form-Row">
          <FormInput
            placeholder={"Cash discount"}
            name={"cash_discount"}
            type={"checkbox"}
            errors={errors}
            onChange={(val: any) => {
              console.log(val);
              setValue("cash_discount", val === "on");
              setValue("percentage_discount", false);
            }}
            register={register}
            required={{}}
          />
          <FormInput
            placeholder={"Percentage discount"}
            name={"percentage_discount"}
            type={"checkbox"}
            onChange={(val: any) => {
              console.log(val);
              setValue("cash_discount", false);
              setValue("percentage_discount", val === "on");
            }}
            errors={errors}
            register={register}
            required={{}}
          />
        </div>
        <p className="Promocode-Create-Form-Title">Discount</p>
        <FormInput
          placeholder={"Discount"}
          name={"discount"}
          type={"number"}
          errors={errors}
          register={register}
          required={{ required: true }}
        />
        <input
          type="submit"
          className="Promocode-Create-Form-Button"
          value="Create"
        />
      </form>
    </div>
  );
};

const mapStateToProps = (state: RootState) => {
  return {};
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  fetchCreatePromocode: (created_promocode: Promocode, history: any) =>
    dispatch(fetchCreatePromocode(created_promocode, history)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CreatePromocodeContainer);
